import "babel-polyfill";
import "classlist-polyfill";
import '../style/app.scss';
import Vue from 'vue';
import router from './router.js'
import App from './components/App.vue';



// Vue.config.productionTip = false

let mixins = {
  data: function () {
    return {
      navExpanded: false,
    }
  },
  mounted(){
    console.log('expanded mixin available');
  }
}

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  mixins: [mixins]
});



